.projects {
  padding: 0 1rem;
  margin: auto;
  max-width: 48rem;
}
.projects > div {
  margin-top: 2rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}
.project-shown {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: end;
      align-items: flex-end;
  background-color: #000;
  background-size: contain;
  background-position: top;
  background-repeat: no-repeat;
  min-height: 15rem;
  width: 30rem;
  max-width: 100%;
  -webkit-box-shadow: 0px 2px 5px rgba(40,40,40,0.4);
          box-shadow: 0px 2px 5px rgba(40,40,40,0.4);
  -webkit-animation: 0.25s 'show-project' ease-in-out;
          animation: 0.25s 'show-project' ease-in-out;
}
.project-shown > div {
  -ms-flex: 1 1;
      flex: 1 1;
  background-color: rgba(40,40,40,0.95);
  color: #eee;
  text-align: left;
  padding: 0.5rem;
}
.shown-title, .shown-description {
  margin: 0;
}
.shown-title {
  font-weight: 500;
}
.shown-description {
  color: #ccc;
}
.shown-links {
  margin-top: 0.5rem;
}
.shown-links a, .shown-links a:visited {
  color: #eee;
}
.shown-links span {
  margin: 0 0.5rem;
}
.projects-list {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: center;
      justify-content: center;
}
.projects-list::-webkit-scrollbar {
    width: 2px;
}
.projects-list::-webkit-scrollbar-track {
  background-color: #ccc;
}
.projects-list::-webkit-scrollbar-thumb {
  background-color: #333;
}
.project-item {
  background-size: cover;
  background-position: top;
  height: 3rem;
  width: 6rem;
  margin: 1rem;
  margin-bottom: 0;
  -webkit-box-shadow: 0px 2px 2px rgba(40,40,40,0.4);
          box-shadow: 0px 2px 2px rgba(40,40,40,0.4);
  cursor: pointer;
  -webkit-transition: opacity 0.2s ease-in-out;
  -o-transition: opacity 0.2s ease-in-out;
  transition: opacity 0.2s ease-in-out;
}
.selected {
  opacity: 0.4;
}
@media screen and (min-width: 450px) {
  .project-shown {
    background-size: cover;
  }
}
@media screen and (min-width: 768px) {
  .projects > div {
    -ms-flex-direction: row;
        flex-direction: row;
  }
  .project-shown {
    height: 19rem;
    width: 38rem;
  }
  .project-shown > div {
    padding: 1rem;
  }
  .projects-list {
    width: 8rem;
    height: 19rem;
    padding: 0 1rem;
    overflow-y: scroll;
  }
  .project-item {
    height: 4rem;
    width: 8rem;
    margin: 0.5rem 0;
  }
  .project-item:first-child {
    margin-top: 0;
  }
  .project-item:last-child {
    margin-bottom: 0;
  }
}

@-webkit-keyframes show-project {
  50% {
    -webkit-filter: brightness(0.4);
            filter: brightness(0.4);
  }
}

@keyframes show-project {
  50% {
    -webkit-filter: brightness(0.4);
            filter: brightness(0.4);
  }
}


