.landing {
  min-height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}
.name {
  margin: 0 0;
}
.name + div {
  color: #444;
}
.title {
  font-weight: 400;
  margin: 0;
}
.address {
  font-weight: 400;
  margin: 0;
}
.title + span {
  display: none;
  font-size: 1.5rem;
  margin: 0 1rem;
}
.landing-img {
  max-height: 20rem;
  -webkit-filter: brightness(0.5);
          filter: brightness(0.5);
  margin-top: 0.5rem;
}
.email {
  margin-top: 0.5rem;
}
.links {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: center;
      justify-content: center;
  margin-top: 0.5rem;
}
.links span {
  margin: 0 0.5rem;
}
@media screen and (min-width: 768px) {
  .landing > div {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
  }
  .title + span {
    display: inherit;
  }
}


